.menu {
  background-color: $colorMenu;

  &__content {
    box-sizing: border-box;
    height: 100vh;
    padding: $headerTopPadding 0;

    @include onTablet {
      padding-top: 52px;
    }
  }

  &__top {
    margin-bottom: 80px;
    @include top-actions;
  }

  &__list {
    margin: 0 0 40px;
    padding: 0;
    list-style: none;
  }

  &__item {
    margin-bottom: 24px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__link {
    display: inline-block;
    padding-bottom: 8px;
    border-bottom: 1px solid transparent;

    font-weight: 700;
    font-size: 22px;
    line-height: 22px;
    letter-spacing: 2px;
    text-transform: uppercase;
    color: $colorText;
    text-decoration: none;
    transition: border-color 0.3s;

    &:hover {
      border-color: $colorText;
    }

  }

  &__call {
    display: block;
    width: max-content;

    font-weight: 700;
    font-size: 16px;
    line-height: 27px;
    text-transform: uppercase;
    color: $colorText;
    text-decoration: none;

    &--phone {
      margin-bottom: 16px;
    }

    &--action {
      font-size: 12px;
      line-height: 16px;
      letter-spacing: 2px;

      padding-bottom: 8px;
      border-bottom: 1px solid $colorText;
    }
  }
}
